<template>
	<div class="index">
		<div class="index-item">
			<video src="https://redbp.oss-cn-shenzhen.aliyuncs.com/test/视频文件.mp4" width="100%" autoplay="autoplay">
					您的浏览器不支持 video 标签。
			</video>
		</div>
		<div class="index-item item-left" :style="{height: ctHeight}">
			<!-- 1060*752 -->
			<!-- 清晰写作框架 -->
			<div id="xzkj" class="svga-item" ref="canvas" :style="{width: ctWidth + 'px', height: ctWidth * 752/1000 + 'px'}"></div>
			<div class="text-div">
				<p>清晰写作框架</p>
				<span>跟着框架一步一步完善项目，不用再担心没有写作思路</span>
			</div>
		</div>
		<div class="index-item item-right" :style="{height: ctHeight}">
			<div class="text-div">
				<p>专业写作指导</p>
				<span>从专业投资人视角为您完善商业逻辑，优化语言表达</span>
			</div>
			<!-- 专业写作指导 -->
			<div id="xzzd" class="svga-item" ref="canvas" :style="{width: ctWidth + 'px', height: ctWidth * 752/1000 + 'px'}"></div>
		</div>
		<div class="index-item item-left" :style="{height: ctHeight}">
			<!-- 优秀案例参考 -->
			<div id="alck" class="svga-item" ref="canvas" :style="{width: ctWidth + 'px', height: ctWidth * 752/1000 + 'px'}"></div>
			<div class="text-div">
				<p>优秀案例参考</p>
				<span>找到成功背后的关键因素，启发您的商业思维</span>
			</div>
		</div>
		<div class="index-item item-right" :style="{height: ctHeight}">
			<div class="text-div">
				<p>自动设计排版</p>
				<span>摆脱繁琐的PPT制作模式，不是设计师也能做出精美BP</span>
			</div>
			<!-- 自动设计排版 -->
			<div id="sjpb" class="svga-item" ref="canvas" :style="{width: ctWidth + 'px', height: ctWidth * 752/1000 + 'px'}"></div>
		</div>
		<div class="index-item item-left" :style="{height: ctHeight}">
			<!-- 在线数据查询 -->
			<div id="sjcx" class="svga-item" ref="canvas" :style="{width: ctWidth + 'px', height: ctWidth * 752/1000 + 'px'}"></div>
			<div class="text-div">
				<p>在线数据查询</p>
				<span>从平台数据库中直接获取行业数据，提高写作效率</span>
			</div>
		</div>
		<div class="index-item item-right" :style="{height: ctHeight}">
			<div class="text-div">
				<p>智能项目诊断</p>
				<span>根据投资人的项目评分方式，对项目进行全方位评估</span>
			</div>
			<!-- 智能项目诊断 -->
			<div id="xmzd" class="svga-item" ref="canvas" :style="{width: ctWidth + 'px', height: ctWidth * 752/1000 + 'px'}"></div>
		</div>
		<div class="index-item" :style="{height: ctHeight}">
			<!-- 免费项目直投 -->
			<div id="xmzt" class="svga-item" ref="canvas" :style="{width: ctWidth + 'px', height: ctWidth * 752/1000 + 'px'}"></div>
			<div class="text-div">
				<p>免费项目直投</p>
				<span>根据项目特性匹配最适合投资人，提高融资成功率</span>
			</div>
		</div>
	</div>
</template>

<script>
import SVGA from "svgaplayerweb";
export default {
	name: 'Index',
	data () {
		return {
			ctWidth: (document.documentElement.clientWidth || document.body.clientWidth)/2,
			ctHeight: (document.documentElement.clientHeight || document.body.clientHeight) + 'px',
			swiperOption: {
				// 自动播放
				autoplay: 2500,
				pagination: '.swiper-pagination',
				// 圆点是否可以点击
				paginationClickable: true,
				// 手动切换轮播后是否禁止自动播放
				autoplayDisableOnInteraction: false,
				// 方向
				direction: 'vertical'
			}
		}
	},
	methods: {
		callback(){
			
		},
		// 展示svga
		showSvga(file, dom) {
			var player = new SVGA.Player(dom);
			var parser = new SVGA.Parser(dom);
			parser.load(file, function(videoItem) {
				player.setVideoItem(videoItem);
				player.startAnimation();
			});
		}
	},
	mounted () {
		// 清晰写作框架
		this.showSvga(require('../assets/donghua/清晰框架.svga'), '#xzkj');
		// 专业写作指导
		this.showSvga(require('../assets/donghua/写作指导.svga'), '#xzzd');
		// 优秀案例参考
		this.showSvga(require('../assets/donghua/案例参考.svga'), '#alck');
		// 自动设计排版
		this.showSvga(require('../assets/donghua/自动设计排版.svga'), '#sjpb');
		// 在线数据查询
		this.showSvga(require('../assets/donghua/在线数据.svga'), '#sjcx');
		// 智能项目诊断
		this.showSvga(require('../assets/donghua/智能项目诊断.svga'), '#xmzd');
		// 免费项目直投
		this.showSvga(require('../assets/donghua/项目直投.svga'), '#xmzt');
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
	* {
		margin: 0;
		padding: 0;
	}
	.index {
		overflow: hidden;
	}
	.index-item {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.text-div {
		display: flex;
		flex-direction: column;
		text-align: left;
		color: #555555;
		margin-left: 60px;
		max-width: 500px;
		p {
			font-size: 72px;
		}
		span {
			font-size: 30px;
			margin-top: 15px;
		}
	}
	.item-left::before{
		content: '';
		width: 0;
		height: 0;
		border-top: 300px solid rgba(0,0,0,0);
		border-right: 300px solid rgba(0,0,0,0);
		border-bottom: 300px solid rgba(0,0,0,0);
		border-left: 300px solid #FF1653;
		position: absolute;
		bottom: 20%;
		left: 0;
		z-index: -1;
		transform: translate(0, 50%);
	}
	.item-right::before{
		content: '';
		width: 0;
		height: 0;
		border-top: 300px solid rgba(0,0,0,0);
		border-right: 300px solid #FF1653;
		border-bottom: 300px solid rgba(0,0,0,0);
		border-left: 300px solid rgba(0,0,0,0);
		position: absolute;
		bottom: 20%;
		right: 0;
		z-index: -1;
		transform: translate(0, 50%);
	}
</style>



