// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'lib-flexible'
import store from './store'
import 'ant-design-vue/dist/antd.css';
import {
  Row,
  Col,
  Avatar,
  Card,
  Input,
  Upload,
  Modal,
  Icon,
  Tabs,
  Divider,
  Tooltip,
  message,
  Table,
  DatePicker,
  Form,
  Checkbox,
  Button,
  Select,
  Spin,
  Dropdown,
  Menu,
  LocaleProvider,
  Timeline,
  InputNumber,
  Tag,
  Slider,
  Pagination
} from 'ant-design-vue'

Vue.use(Row) // 栅格化
Vue.use(Col) // 栅格化
Vue.use(Avatar) // 头像
Vue.use(Card) // 卡片
Vue.use(Input) // 输入框
Vue.use(Upload) // 上传组件
Vue.use(Modal) // 模态框
Vue.use(Icon) // 图标
Vue.use(Tabs) // 切换标签
Vue.use(Divider) // 分隔线
Vue.use(Tooltip) // 提示
Vue.use(Table) // 表格
Vue.use(DatePicker) // 日期
Vue.use(Form) // 表单
Vue.use(Checkbox) // 勾选框
Vue.use(Button) // 按钮
Vue.use(Select) // 下拉选择
Vue.use(Spin) // 加载
Vue.use(Dropdown) // 下拉
Vue.use(Menu) // 菜单栏
Vue.use(LocaleProvider) // 地区语言设置
Vue.use(Timeline) // 时间轴
Vue.use(InputNumber) // 数字输入框
Vue.use(Tag)  // 标签
Vue.use(Slider)  // 滑动输入条
Vue.use(Pagination)  // 分页

Vue.prototype.$message = message // 信息提示
Vue.prototype.$confirm = Modal.confirm // 确认信息

import 'swiper/css/swiper.css'
import VueAwesomeSwiper from 'vue-awesome-swiper';
// import SVGA from "svgaplayerweb";
Vue.use(VueAwesomeSwiper);

Vue.config.productionTip = false;
// 注册全局组件，比如底部栏、头部栏、toast组件等
import Com from '@/components/js/components'
Vue.prototype.Com = Com;
// Vue.prototype.$SVGA = SVGA;
Com.init();

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



