<template>
	<div class="topbar" v-if="showBar">
		<img :src="require('@/assets/bp-icon.png')" alt="">
		<div class="topbar-tab">
			<div class="topbar-item" @click="linkUrl('anli')">
				<div class="topbar-item-text" :class="{'active-text': clickTap==='anli'}">案例</div>
				<div class="topbar-item-seat" :class="{'active-seat': clickTap==='anli'}"></div>
			</div>
			<div class="topbar-item" @click="linkUrl('ganhuo')">
				<div class="topbar-item-text" :class="{'active-text': clickTap==='ganhuo'}">干货</div>
				<div class="topbar-item-seat" :class="{'active-seat': clickTap==='ganhuo'}"></div>
			</div>
			<div class="topbar-item" @click="linkUrl('rongzi')">
				<div class="topbar-item-text" :class="{'active-text': clickTap==='rongzi'}">融资</div>
				<div class="topbar-item-seat" :class="{'active-seat': clickTap==='rongzi'}"></div>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		data() {
			return {
				clickTap: '',
				showBar: true
			}
		},
		methods: {
			// 路由跳转
			linkUrl(item) {
				this.clickTap = item;
				if (item === 'anli'){
					// 路由跳转的两种方式
					this.$router.push({name: 'Anli', params: {title: '案例'}})
					// this.$router.push({path: 'Index', query:{title: 首页}})
				} else if(item === 'ganhuo') {
					this.$router.push({name: 'Ganhuo', params: {title: '干货'}})
				} else if(item === 'rongzi') {
					this.$router.push({name: 'Rongzi', params: {title: '融资'}})
				}
			},
			// 显示或者隐藏topbar
			showOrHideTopBar(bool) {
				this.showBar = bool;
			}
		},
		mounted() {
			if (this.$route.name === 'Anli') {
				this.clickTap = 'anli';
			} else if (this.$route.name === 'Ganhuo')  {
				this.clickTap = 'ganhuo';
			} else if (this.$route.name === 'Rongzi') {
				this.clickTap = 'rongzi';
			} else {
				this.clickTap = '';
			}
			// 隐藏顶部导航栏
			if (this.$route.name === 'Detailshow') {
				this.showOrHideTopBar(false);
			} else {
				this.showOrHideTopBar(true);
			}
		},
		watch: {
			$route() {
				if (this.$route.name === 'Anli') {
					this.clickTap = 'anli';
				} else if (this.$route.name === 'Ganhuo')  {
					this.clickTap = 'ganhuo';
				} else if (this.$route.name === 'Rongzi') {
					this.clickTap = 'rongzi';
				} else {
					this.clickTap = '';
				}
				// 隐藏顶部导航栏
				if (this.$route.name === 'Detailshow') {
					this.showOrHideTopBar(false);
				} else {
					this.showOrHideTopBar(true);
				}
			}
		}
	}
</script>
<style lang="less">
	.topbar {
		position: fixed;
		top: 0;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		z-index: 100;
		height: 64px;
		line-height: 64px;
		font-size: 23px;
		box-shadow: inset 0 -4px 3px -1px rgba(0, 0, 0, 0.1);
		background-color: #ffffff;
		img {
			// width: 137px;
			// height: 44px;
			width: 156px;
			height: 50px;
			margin-left: 25px;
		}
		&-tab {
			display: flex;
			height: 55px;
			align-items: center;
		}
		&-item {
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 80px;
			height: 64px;
			cursor: pointer;
			&-text {
				height: 60px;
			}
			&-seat {
				width: 50px;
				height: 3px;
				border-radius: 20px;
				opacity: 0;
			}
			.active-text {
				color: #FF4444;
			}
			.active-seat {
				background-color: #FF4444;
				border-radius: 2px;
				opacity: 1;
			}
		}
	}
</style>


