<template>
	<div class="rongzi">
		<div class="banner">
			<div class="font-69">优秀项目案例</div>
			<div class="font-42">找到成功背后的关键因素，启发您的商业思维</div>
		</div>
		<div class="tag">
			<div class="tag-key">
				<span>行业：</span>
				<div class="tag-item" v-for="(key, index) in tabKeyArr" :key="parseInt(index)">
					<a-checkable-tag
						:key="key.name"
						:checked="selectedTags.indexOf(key.name) > -1"
						@change="checked => handleChange(key.name, checked)"
					>
						{{ key.name }}
					</a-checkable-tag>
				</div>
			</div>
			<div class="tag-value">
				<div class="tag-item" v-for="(tag, index) in tabValueArr" :key="index">
					<a-checkable-tag
						:key="tag"
						:checked="selectedValueTags.indexOf(tag) > -1"
						@change="checked => handleValueChange(tag, checked)"
					>
						{{ tag }}
					</a-checkable-tag>
				</div>
			</div>
		</div>
		<a-row class="rongzi-people">
			<div class="people-item" v-for="(item, index) in peopleArr" :key="index">
				<div class="item-part1">
					<img :src="require('@/assets/logo.png')" alt="">
					<div class="item-part1-info">
						<span class="font-41">{{item.name}}</span>
						<span class="font-28">{{item.position}}</span>
					</div>
					<div class="item-part1-btn">投递项目</div>
				</div>
				<a-divider />
				<div class="item-part2">
					<div class="item-part2-title">投资领域</div>
					<div class="item-part2-territory">{{item.territory}}</div>
					<div class="item-part2-num">已有<span>{{item.figures}}人</span>投递</div>
				</div>
			</div>
		</a-row>
		<a-pagination class="fenye-container"
			v-model="current"
			:page-size-options="pageSizeOptions"
			:total="total"
			show-size-changer
			:page-size="pageSize"
			@showSizeChange="onShowSizeChange"
		>
			<template slot="buildOptionText" slot-scope="props">
				<span v-if="props.value !== '50'">{{ props.value }}条/页</span>
				<span v-if="props.value === '50'">全部</span>
			</template>
		</a-pagination>
	</div>
</template>

<script>
import axios from 'axios';
export default {
	name: 'Index',
	data () {
		return {
			tabKeyArr: ['互联网', '移动互联网', '医疗健康', '环保能源', '制造业', '本地生活', '教育', '金融', '文化娱乐', '硬件', '农业', '游戏'],
			tabValueArr: ['互联网', '移动互联网', '医疗健康', '环保能源', '制造业', '本地生活', '教育', '金融', '文化娱乐', '硬件', '农业', '游戏'],
			selectedTags: [],
			selectedValueTags: [],
			// 
			pageSizeOptions: ['10', '20', '30', '40', '50'],
			current: 1,
			pageSize: 20,
			total: 500,
			// 
			// company: "21世纪天使资本"
			// email: "yanghaitao@21vc.com"
			// id: 1
			// job: "投资总监"
			// mobile: ""
			// name: "杨海涛"
			peopleArr: [{
				name: '马云',
				position: '阿里巴巴创始人',
				territory: '文化创意、游戏娱乐、企业服务、金融、其他',
				figures: 200
			}, {
				name: '马云',
				position: '阿里巴巴创始人',
				territory: '文化创意、游戏娱乐、企业服务、金融、其他',
				figures: 200
			}, {
				name: '马云',
				position: '阿里巴巴创始人',
				territory: '文化创意、游戏娱乐、企业服务、金融、其他',
				figures: 200
			}, {
				name: '马云',
				position: '阿里巴巴创始人',
				territory: '文化创意、游戏娱乐、企业服务、金融、其他',
				figures: 200
			}, {
				name: '马云',
				position: '阿里巴巴创始人',
				territory: '文化创意、游戏娱乐、企业服务、金融、其他',
				figures: 200
			}, {
				name: '马云',
				position: '阿里巴巴创始人',
				territory: '文化创意、游戏娱乐、企业服务、金融、其他',
				figures: 200
			}, ]
		}
	},
	methods: {
		callback(){
			
		},
		handleChange(tag, checked) {
			const { selectedTags } = this;
			const nextSelectedTags = checked
				? [...selectedTags, tag]
				: selectedTags.filter(t => t !== tag);
			console.log('You are interested in: ', nextSelectedTags);
			this.selectedTags = nextSelectedTags;
		},
		handleValueChange(tag, checked) {
			const { selectedValueTags } = this;
			const nextSelectedTags = checked
				? [...selectedValueTags, tag]
				: selectedValueTags.filter(t => t !== tag);
			console.log('You are interested in: ', nextSelectedTags);
			this.selectedValueTags = nextSelectedTags;
		},
		onShowSizeChange(current, pageSize) {
			this.pageSize = pageSize;
		},
		// 获取行业信息
		getHangyeData() {
			axios.get(`https://www.youjunming.cn/index.php/v1/domain`, {
				params: {
					
				}
			})
			.then((res) => {
				this.tabKeyArr = res.data.data;
			})
		},
		// 获取投资人列表数据
		getPeopleData() {
			axios.get(`https://www.youjunming.cn/index.php/v1/investor/list`, {
				params: {
					page: 1,
					size: 20,
				}
			})
			.then((res) => {
				// this.peopleArr = res.data.results;
				console.log(res.data)
			})
		}
	},
	mounted () {
		this.getHangyeData();
		this.getPeopleData();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
	.rongzi {
		width: 100%;
		height: 100%;
		margin-top: 64px;
		background-color: #F7F7F7;
	}
	.banner {
		width: 100%;
		height: 300px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background-color: #cccccc;
		color: #ffffff;
		padding-bottom: 15px;
		.font-69 {
			font-size: 69px;
		}
		.font-42 {
			font-size: 42px;
		}
	}
	.ant-tag-checkable-checked {
		color: #FF0042;
		background-color: #ffffff;
		font-weight: bold;
	}
	.ant-tag {
		font-size: 19px !important;
		&:hover {
			color: #FF0042;
		}
	}
	.tag {
		width: 90%;
		margin: auto;
		margin-top: 20px;
		box-shadow:#e1e1e1 0px 0px 10px;
		font-size: 17px;
		padding: 15px 25px;
		line-height: 35px;
		text-align: left;
		&-key {
			span {
				display: inline-block;
				padding-left: 7px;
				font-size: 19px;
			}
		}
		&-item {
			display: inline-block;
		}
		&-value {
			
		}
	}
	.rongzi-people {
		width: 92%;
		margin: auto;
		display: flex;
		flex-wrap: wrap;
	}
	.people-item {
		width: 48%;
		background-color: #ffffff;
		margin: 30px 1% 0;
		padding: 25px 35px;
	}
	.item-part1 {
		display: flex;
		justify-content: space-between;
		align-items: center;
		img {
			width: 150px;
			height: 150px;
			border-radius: 50%;
		}
		&-info {
			flex: 1;
			display: flex;
			flex-direction: column;
			margin-left: 30px;
			.font-41 {
				font-size: 41px;
				text-align: left;
			}
			.font-28 {
				font-size: 28px;
				text-align: left;
			}
		}
		&-btn {
			width: 159px;
			height: 53px;
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: #FF4444;
			border-radius: 5px;
			font-size: 24px;
			color: #ffffff;
		}
	}
	.item-part2 {
		text-align: left;
		&-title {
			font-size: 27px;
			color: #333333;
		}
		&-territory {
			font-size: 28px;
			color: #666666;
			margin-top: 5px;
		}
		&-num {
			font-size: 28px;
			color: #C1C1C1;
			margin-top: 10px;
			span {
				font-size: 34px;
				color: #DE6E00;
			}
		}
	}
	.fenye-container {
		margin: 60px auto 65px;
	}
</style>



