import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/Index'
import Anli from '@/views/Anli'
import Ganhuo from '@/views/Ganhuo'
import Rongzi from '@/views/Rongzi'
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    // 案例
    {
      path: '/anli',
      name: '',
      component: Anli,
      children: [{
        path: 'index',
        name: 'Anli',
        component: () => import('@/views/anli/Index')
      }, {
        path: 'detail',
        name: 'Detail',
        component: () => import('@/views/anli/Detail')
      }, {
        path: 'detailshow',
        name: 'Detailshow',
        component: () => import('@/views/anli/Detailshow')
      }]
    },
    // 干货
    {
      path: '/ganhuo',
      name: '',
      component: Ganhuo,
      children: [{
        path: 'index',
        name: 'Ganhuo',
        component: () => import('@/views/ganhuo/Index')
      }, {
        path: 'article',
        name: 'Article',
        component: () => import('@/views/ganhuo/Article')
      }]
    },
    // 融资
    {
      path: '/rongzi',
      name: 'Rongzi',
      component: Rongzi
    },
  ]
})



