const state = {
	// loading组件对象
	loadingObj: {
		show: false,
	},
}
const mutations = {
	// 显示loading组件
	SHOW_LOADING () {
		state.loadingObj.show = true;
	},
	// 隐藏loading组件
	HIDE_LOADING () {
		state.loadingObj.show = false;
	}
}
export default {
	state, mutations
}


